import React from 'react'
import { useSelector } from 'react-redux'
import Signup from "./signupview"
import ReportView from './reportview'
import IndexView from './indexview'
import PlanView from "./planview"
import DashboardView from './dashboardview'
import AdminUserView from './auserview'
import PasswordResetView from './common/passwordreset'
import QuestionMgmtView from './questionmgmtview'
import QuestionnaireView from './questionnaireview'

import { BrowserRouter, Routes, Route } from 'react-router-dom'

function RootView() {
	
	const token = useSelector((state) => state.authorization.token)
	const scope = useSelector((state) => state.authorization.scope)
	
	if(scope === 'admin') {
		return <DashboardView admin={true} />
	} else if(scope === 'company') {
		return <DashboardView admin={false} />
	} else {
		return <DashboardView admin={false} />
	}
}


export default function App() {

  return (
    <div className="app">
    <BrowserRouter>
    	<Routes>
			<Route path="/" element={<RootView />} />
			<Route path="/qmgmt" element={<QuestionMgmtView />} />
			<Route path="/login" element={<RootView />} />
			<Route path="/users" element={<AdminUserView />} />
			<Route path="/company" element={<Signup />} />
			<Route path="/report" element={<ReportView />} />
			<Route path="/index" element={<IndexView />} />
			<Route path="/plan" element={<PlanView />} />
			<Route path="/reset/:url" element={<PasswordResetView/>} /> 
			<Route path="/questionnaire/:token" element={<QuestionnaireView/>} />
    	</Routes>
    </BrowserRouter>
    </div>
  );
}
