export default function PlanView() {

	const svgRef = useRef(null);
	
	const [ dx, dy ] = [ 6, 120 ];
	const width  = 800;
	const diagonal = d3.linkHorizontal().x(d => d.y).y(d => d.x);
	const tree = d3.tree().nodeSize([dx, dy])
	const margin = ({top: 10, right: 0, bottom: 10, left: 40})
	
	useEffect(() => {
		const root = d3.hierarchy(data);
		const svg = d3.select(svgRef.current)
			.attr("viewBox", [-margin.left, -margin.top, width, dx])
			.style("background", "white")
      		.style("font", "5px sans-serif")
      		.style("user-select", "none");
		const gLink = d3.select(svgRef.current).select("g.links")
      		.attr("fill", "none")
      		.attr("stroke", "#555")
      		.attr("stroke-opacity", 0.1)
      		.attr("stroke-width", 0.5);
		const gNode = d3.select(svgRef.current).select("g.nodes") 
			.attr("cursor", "pointer")
      		.attr("pointer-events", "all");
		const gLabel = d3.select(svgRef.current).select("g.labels"); 
		const gLegend = d3.select(svgRef.current).select("g.legend");
		
		const duration = 250;
    	const nodes = root.descendants().reverse();
    	const links = root.links();
		
		// Compute the new tree layout.
    	tree(root);

    	let left = root;
    	let right = root;
    	root.eachBefore(node => {
      		if (node.x < left.x) left = node;
      		if (node.x > right.x) right = node;
    	});
    	
    	const height = right.x - left.x + margin.top + margin.bottom;

    	const transition = svg.transition()
        	.duration(duration)
        	.attr("viewBox", [-margin.left, left.x - margin.top, width, height])
        	.tween("resize", window.ResizeObserver ? null : () => () => svg.dispatch("toggle"));

    	// Update the nodes�
    	gNode.selectAll("g")
    		.data(nodes, d => d.id)
    		.join(
				enter => {
					const nodeEnter = enter
						.append("g")
						.attr("transform", d => `translate(${d.y},${d.x})`)
        				.attr("fill-opacity", 1)
        				.attr("stroke-opacity", 1);
        			nodeEnter.append("circle")
        				.attr("r", 2.5)
        				.attr("fill", d => d._children ? "#555" : "#999")
        				.attr("stroke-width", 10);
    				nodeEnter.append("text")
        				.attr("dy", "0.31em")
        				.attr("x", d => d._children ? -6 : 6)
        				.attr("text-anchor", d => d._children ? "end" : "start")
        				.text(d => d.data.name)
      					.clone(true).lower()
        					.attr("stroke-linejoin", "round")
        					.attr("stroke-width", 3)
        					.attr("stroke", "white");
				},
				update => update.attr("transform", d => `translate(${d.y},${d.x})`),
				exit => exit.transition(d3.easeLinear).duration(300).style("opacity", 0).remove()
			)

		const filaaja = nodes.filter(n => n.data.fi).map(n => { return {...n , y: n.y + 320, label: 'green'}});
		const kvlaaja = nodes.filter(n => n.data.kv).map(n => { return {...n , y: n.y + 340, label: 'gray'}});
		const fisuppea = nodes.filter(n => n.data.fis).map(n => { return {...n , y: n.y + 325, label: 'red'}});
		const kvsuppea = nodes.filter(n => n.data.kvs).map(n => { return {...n , y: n.y + 345, label: 'blue'}});
		
		const labels = [...filaaja, ...fisuppea, ...kvlaaja, ...kvsuppea];
		
		gLabel.selectAll("g")
			.data(labels, d => d.id) 
			.join(
				enter => enter.append("g")
						.attr("transform", d => `translate(${d.y},${d.x})`)
        				.attr("fill-opacity", 1)
        				.attr("stroke-opacity", 1)
        				.append("circle")
        				.attr("r", 2.5)
        				.attr("fill", d => d.label)
        				.attr("stroke-width", 10),
				update => update.attr("transform", d => `translate(${d.y},${d.x})`),
				exit => exit.remove()			
			);
		
		gLink.selectAll('path').data(links, d => d.target.id)
			.join(
				enter => enter.append("path").attr("d", diagonal),
				update => update.attr("d", diagonal),
				exit => exit.remove()
			)
			
			
		gLegend.selectAll('g').data([
			{ x: -372, y: 0, name:'FI-LAAJA', color: 'green'},
			{ x: -366, y: 0, name:'FI-SUPPEA', color: 'red'},
			{ x: -360, y: 0, name:'KV-LAAJA', color: 'gray'},
			{ x: -354, y: 0, name:'KV-SUPPEA', color: 'blue'},
		]).join(
			enter => {
				const nodeEnter = enter
					.append("g")
					.attr("transform", d => `translate(${d.y},${d.x})`)
        			.attr("fill-opacity", 1)
        			.attr("stroke-opacity", 1);
        		nodeEnter.append("circle")
        			.attr("r", 2.5)
        			.attr("fill", d => d.color)
        			.attr("stroke-width", 10);
    			nodeEnter.append("text")
        			.attr("dy", "0.31em")
        			.attr("x", d => d._children ? -6 : 6)
        			.attr("text-anchor", "start")
        			.text(d => d.name)
      				.clone(true).lower()
        				.attr("stroke-linejoin", "round")
        				.attr("stroke-width", 3)
        				.attr("stroke", "white");
				},
				update => update.attr("transform", d => `translate(${d.y},${d.x})`),
				exit => exit.transition(d3.easeLinear).duration(300).style("opacity", 0).remove()
		)
		
	}, [])
	
	return (
	<>
		<Menu />
		<LoginModal />
		<Container fluid>
			<Row>
			<Col sm={5} md={6} lg={7}>
			<h4>Suunnitelmat</h4>
			</Col>
			</Row>	
			<Row>
			<svg className="d-block mx-auto" strokeLinejoin="round" strokeLinecap="round" ref={svgRef} >
				<g className="links"/>
				<g className="nodes"/>
				<g className="labels" />
				<g className="legend" />
			</svg>
			</Row>
		</Container>
	</>
	)
}